<template>
  <div>
    <form action="/login" method="get" id="login">
      <input
        v-model.trim="userID"
        type="text"
        name="userID"
        class="key"
        placeholder=" "
      />
      <br />
      <input
        v-model.trim="password"
        type="password"
        name="password"
        class="key"
        placeholder=" "
      />
      <br />
      <img @click="changeImg" id="verifyCodeImg" :src="verifyCodeImg" />
      <input
        v-model.trim="verifyCodeStr"
        id="verifyCodeStr"
        type="text"
        placeholder=""
      />
      <br />
      <span style="color: red">{{ error }}</span>
      <transition-group enter-active-class="animate__swing">
        <button
          v-show="isShow"
          @click="login"
          class="submit"
          :class="{ animate__animated: isShow }"
          type="button"
          key="1"
        >
          
        </button>
        <button
          v-show="!isShow"
          @click="register"
          class="submit"
          :class="{ animate__animated: !isShow }"
          type="button"
          key="2"
        >
          
        </button>
      </transition-group>
      <a
        v-show="isShow"
        @click.prevent="isShow = !isShow"
        href="http://www.baidu.com"
        class="tip"
        >?</a
      >
      <a v-show="!isShow" @click.prevent="isShow = !isShow" href="#" class="tip"
        ></a
      >
    </form>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import "animate.css";
export default {
  name: "Login",
  data() {
    return {
      userID: "",
      password: "",
      verifyCodeStr: "",
      verifyCodeImg: "http://localhost:8080/login/verifyCodeImg",
      error: "",
      //or
      isShow: true,
    };
  },
  methods: {
    ...mapActions("group", ["initHost"]),
    changeImg() {
      //
      //
      this.verifyCodeImg =
        "http://localhost:8080/login/verifyCodeImg?t=" + Date.now();
    },
    checkComplete() {
      //     
      if (!this.userID || !this.password) {
        alert("");
        return false;
      } else if (!this.verifyCodeStr) {
        alert("");
        return false;
      }
      return true;
    },
    login() {
      if (!this.checkComplete()) return;
      //  
      this.$axios
        .get("http://localhost:8080/login/verifyCodeStr", {
          params: {
            verifyCodeStr: this.verifyCodeStr,
          },
        })
        .then((res) => {
          //res.datatrue | false
          if (!res.data) {
            //
            this.error = ",";
            //
            this.changeImg();
          } else {
            //,
            this.$axios
              .get("http://localhost:8080/login", {
                params: {
                  userID: this.userID,
                  password: this.password,
                },
              })
              .then((res) => {
                //
                if (!res.data || !res.data.userID) {
                  this.error = ",";
                  //
                  this.changeImg();
                } else {
                  //
                  if (res.data.userID === "0") {
                    this.error = ",";
                    return;
                  }
                  //
                  this.initHost(res.data);
                  //
                  //,
                  localStorage.setItem("login", "true");
                  localStorage.setItem("host", JSON.stringify(res.data));
                  this.$router.replace({
                    name: "group",
                  });
                }
              })
              //
              .catch((res) => {
                this.error = ",";
              });
          }
        })
        //
        .catch((res) => {
          this.error = "";
        });
    },
    register() {
      //
      if (!this.checkComplete()) return;
      //userIDpassword,>=7, <= 15>= 6 <= 15
      if (this.userID.length < 7) {
        alert("7");
        this.changeImg();
        return;
      } else if (this.userID.length > 15) {
        alert("15");
        this.changeImg();
        return;
      } else if (
        this.userID.split("").filter((ch) => !(ch >= "0" && ch <= "9"))
          .length !== 0
      ) {
        alert("");
        this.changeImg();
        return;
      } else if (this.password.length < 6) {
        alert("6");
        this.changeImg();
        return;
      } else if (this.password.length > 15) {
        alert("15");
        this.changeImg();
        return;
      }
      // ajax
      this.$axios
        .get("http://localhost:8080/login/verifyCodeStr", {
          params: {
            verifyCodeStr: this.verifyCodeStr,
          },
        })
        .then((res) => {
          //res.datatrue | false
          if (!res.data) {
            //
            this.error = ",";
            //
            this.changeImg();
          } else {
            //,
            this.$axios
              .get("http://localhost:8080/register/user", {
                params: {
                  userID: this.userID,
                  password: this.password,
                },
              })
              .then((res) => {
                //
                if (res.data !== true) {
                  this.error = ",";
                  //
                  this.changeImg();
                } else {
                  //
                  //hostnull
                  // ,
                  let host = {
                    userID: this.userID,
                    password: this.password,
                    userName: null,
                    headImageAddress: null,
                    detail: {
                      userID: this.userID,
                      sex: null,
                      age: null,
                      birthday: null,
                      location: null,
                      persionalSign: null,
                    },
                  };
                  //host
                  this.initHost(host);
                  //
                  //,
                  console.log(res.data);
                  localStorage.setItem("login", "true");
                  localStorage.setItem("host", JSON.stringify(res.data));
                  this.$router.replace({
                    name: "group",
                  });
                  console.log("register");
                }
              })
              //
              .catch((res) => {
                this.error = ",";
              });
          }
        })
        //
        .catch((res) => {
          this.error = "";
        });
    },
  },
};
</script>

<style scoped>
@font-face {
  font-family: "icomoon";
  src: url("/font/fonts/icomoon.eot?h5j9ar");
  src: url("/font/fonts/icomoon.eot?h5j9ar#iefix") format("embedded-opentype"),
    url("/font/fonts/icomoon.ttf?h5j9ar") format("truetype"),
    url("/font/fonts/icomoon.woff?h5j9ar") format("woff"),
    url("/font/fonts/icomoon.svg?h5j9ar#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
body {
  background-color: #efefef;
}

input {
  border: 0;
  border-bottom: 1px solid #e5e5e5;
  outline: none;
}

#login {
  position: relative;
  box-sizing: border-box;
  width: 425px;
  height: 325px;
  margin: 100px auto;
  text-align: center;
  padding-top: 100px;
  background-color: #fff;
}

#login .key {
  height: 30px;
  width: 260px;
  margin-bottom: 10px;
  font-family: icomoon;
  font-size: 12px;
  font-weight: 700;
  line-height: 20px;
}

#login .submit {
  height: 30px;
  width: 260px;
  border: 0;
  border-radius: 2px;
  background-color: #12b7f5;
  line-height: 30px;
  text-align: center;
  color: #fff;
}
#verifyCodeImg {
  width: 160px;
  height: 50px;
  background-color: pink;
  vertical-align: middle;
  margin-bottom: 10px;
}
#verifyCodeStr {
  margin-left: 10px;
  width: 85px;
  height: 15px;
}
.tip {
  position: absolute;
  right: 15px;
  bottom: 20px;
  color: #cfcfcf;
  text-decoration-line: underline;
  cursor: pointer;
}
.disappear {
  display: none;
}
</style>