<template>
  <div class="edit">
    <div id="head">
      <div @click="skipToSetting" class="triangle"></div>
      
    </div>
    <ul>
      <li>
        <input v-model="sex" type="text" placeholder="" />
      </li>
      <hr />
      <li>
        <input v-model="age" type="text" placeholder="" />
      </li>
      <hr />
      <li>
        <input v-model="birthday" type="date" placeholder="" />
      </li>
      <hr />
      <li>
        <input
          v-model="location"
          type="text"
          placeholder=""
        />
      </li>
      <hr />
      <li>
        <input
          v-model="persionalSign"
          type="text"
          placeholder=""
        />
      </li>
    </ul>
    <button @click="modifyDetail" id="editBtn" type="button"></button>
  </div>
</template>


<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "Edit",
  data() {
    //host.detail
    //null
    return {
      sex: "",
      age: "",
      birthday: "",
      location: "",
      persionalSign: "",
    };
  },
  updated() {
    console.log(this.birthday);
  },
  computed: {
    //
    ...mapState("group", ["host"]),
  },
  methods: {
    ...mapMutations("group", ["updateDetail"]),
    //<setting
    skipToSetting() {
      this.$router.replace({
        name: "setting",
      });
    },
    modifyDetail() {
      // ajax
      // 
      if (
        this.sex !== this.host.detail.sex ||
        this.age !== this.host.detail.age ||
        //birthday 20xx-xx-xx,
        (this.birthday &&
          new Date(this.birthday).getTime() !== this.host.detail.birthday) ||
        this.location !== this.host.detail.location ||
        this.persionalSign !== this.host.detail.persionalSign
      ) {
        //
        if (this.sex.length > 1 || (this.sex !== "" && this.sex !== "")) {
          alert("");
        } else if (
          this.age.length > 3 ||
          this.age.split("").filter((ch) => !(ch >= "0" && ch <= "9"))
            .length !== 0
        ) {
          alert("0~999");
        } else if (this.location.length > 100) {
          alert("100");
        } else if (this.persionalSign.length > 100) {
          alert("100");
        } else {
          this.$axios
            .get("http://localhost:8080/detail/put", {
              params: {
                //userID
                userID: this.host.userID,
                sex: this.sex,
                age: this.age,
                birthday: new Date(this.birthday).getTime(),
                location: this.location,
                persionalSign: this.persionalSign,
              },
            })
            .then((res) => {
              //
              if (res.data !== true) {
                alert("\n" + res.message);
              } else {
                alert("");
                console.log("");
                //host.detail
                this.updateDetail({
                  sex: this.sex,
                  age: this.age,
                  birthday: this.birthday
                    ? new Date(this.birthday).getTime()
                    : null,
                  location: this.location,
                  persionalSign: this.persionalSign,
                });
                console.log("");
              }
            })
            .catch((res) => {
              alert("\n" + res.message);
            });
        }
      } else {
        alert("");
      }
    },
  },
  //data()
  activated() {
    console.log("settingactived");
    let date = new Date(this.host.detail.birthday);
    this.sex = this.host.detail.sex;
    this.age = this.host.detail.age;
    this.birthday =
      date.getFullYear() +
      "-" +
      (date.getMonth() + 1 <= 9
        ? "0" + (date.getMonth() + 1)
        : date.getMonth() + 1) +
      "-" +
      (date.getDay() + 1 <= 9 ? "0" + (date.getDay() + 1) : date.getDay() + 1);
    this.location = this.host.detail.location;
    this.persionalSign = this.host.detail.persionalSign;
  },
  beforeUpdate() {
    console.log("editBeforeUpdate");
  },
  updated() {
    console.log("editUpdated");
  },
};
</script>

<style scoped>
hr {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
input {
  margin-left: 50px;
  border: 0;
  outline: none;
  font-size: 16px;
  color: #cfcfcf;
}

.edit {
  position: relative;
  float: left;
  margin-left: 100px;
  width: 980px;
  height: 500px;
  background-color: #fff;
  overflow: hidden;
}

ul li {
  margin: 24px 5px;
}

.triangle {
  position: absolute;
  top: 13px;
  left: 13px;
  right: 30px;
  width: 10px;
  height: 10px;
  border-top: 2px solid #fff;
  border-right: 2px solid #cfff;
  transform: rotate(-135deg);
}

#editBtn,
#head {
  left: 50%;
  transform: translateX(-50%);
  border: 1px solid #cdcfd0;
  color: white;
  background-color: #12b7f5;
  line-height: 40px;
  text-align: center;
  font-size: 20px;
}

#editBtn {
  position: absolute;
  bottom: 0;
  width: 300px;
  height: 40px;
  cursor: pointer;
}

#head {
  position: relative;
  width: 100%;
}
</style>
