<template>
  <div>
    <Nav />
    <Body />
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations } from "vuex";
import Nav from "./Nav.vue";
import Body from "./Body.vue";
import vue from "../main";
export default {
  name: "Native",
  components: { Nav, Body },
  props: ["groupID", "leaderID", "groupName", "headImageAddress"],
  computed: {
    ...mapState("group", ["host", "room", "users"]),
  },
  methods: {
    ...mapActions("group", [
      "initUsers",
      "initRoom",
      "connect",
      "disconnect",
      "destroyData",
      "destroyMsg",
    ]),
    ...mapMutations("group", ["updatePrivateMsg"]),
  },
  activated() {
    // ,,
    let needRequest = false;
    if (this.room.groupID !== this.groupID) {
      this.destroyMsg();
      needRequest = true;
    }
    //ID  ID 
    this.$axios
      .get("http://localhost:8080/list/user", {
        params: {
          userID: this.host.userID,
          groupID: this.groupID,
        },
      })
      .then((res) => {
        if (!res.data) {
          alert("");
          this.$router.replace({
            name: "group",
          });
        } else {
          //
          this.destroyData();
          this.initRoom({
            groupID: this.groupID,
            leaderID: this.leaderID,
            groupName: this.groupName,
            headImageAddress: this.headImageAddress,
          });
          this.initUsers(res.data);
          //
          if (needRequest) {
            for (let user of this.users) {
              this.$axios
                .get("http://localhost:8080/list/record", {
                  params: {
                    groupID: this.room.groupID,
                    userID: this.host.userID,
                    oppositeID: user.userID,
                    time: Date.now(),
                  },
                })
                .then((res) => {
                  if (!res.data || !res.data.length) {
                  } else {
                    for (let i = res.data.length - 1; i >= 0; i--) {
                      let privateMsg = res.data[i];
                      privateMsg.userNameWithoutId =
                        privateMsg.sourceID == user.userID
                          ? user.userName
                          : this.host.userName;
                      privateMsg.headImageAddress =
                        privateMsg.sourceID == user.userID
                          ? user.headImageAddress
                          : this.host.headImageAddress;
                      privateMsg.sourceName =
                        privateMsg.userNameWithoutId +
                        "@" +
                        privateMsg.sourceID;
                      privateMsg.targetName =
                        (privateMsg.targetID == user.userID
                          ? user.userName
                          : this.host.userName) +
                        "@" +
                        privateMsg.targetID;

                      this.updatePrivateMsg(privateMsg);
                    }
                  }
                });
            }
          }
          //connectdestroyData
          this.connect();
        }
      })
      .catch((res) => {
        this.error = "";
        this.$router.replace({
          name: "group",
        });
      });
  },
  deactivated() {
    //
    this.destroyData();
    this.disconnect();
  },
  //
  beforeRouteEnter(to, from, next) {
    // !vuevuevue
    if (!vue) {
      return;
    }
    if (!vue.$store.state.group.host.userID) {
      alert("");
      //
      vue.$router.replace({
        name: "login",
      });
      return;
    }
    //setting
    if (!vue.$store.state.group.host.userName) {
      alert("");
      vue.$router.replace({
        name: "setting",
      });
      return;
    }
    next();
  },
};
</script>

