<template>
  <div>
    <!-- Login  Group  Native -->
    <!-- <Group /> -->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Group from "./components/nav/Group.vue";
export default {
  name: "App",
  data() {
    return {};
  },
  methods: {
    ...mapActions("group", ["initHost", "destroyHost"]),
    //group
    toGroup() {
      //
      if (localStorage.getItem("login")) {
        //
        let host = JSON.parse(localStorage.getItem("host"));
        //
        this.$axios
          .get("http://localhost:8080/login", {
            params: { userID: host.userID, password: host.password },
          })
          .then((res) => {
            //
            if (!res.data || !res.data.userID) {
              //
              localStorage.clear();
              alert(",");
              //
              this.$router.replace({
                name: "login",
              });
            }
            //
            else if (res.data.userID === "0") {
              alert("");
              //
              this.$router.replace({
                name: "login",
              });
            }
            //
            else {
              //
              this.initHost(res.data);
              //
              this.$router.replace({
                name: "group",
              });
            }
          })
          .catch((res) => {
            alert("\n" + res.message);
            localStorage.clear();
            this.$router.replace({
              //
              name: "login",
            });
          });
      } else {
        //
        this.$router.replace({
          name: "login",
        });
      }
    },
  },
  computed: {
    ...mapState("group", ["host"]),
  },

  mounted() {
    console.log("mounted");
    window.onbeforeunload = () => {
      console.log("before");
      //
      // onbeforeunload,google,axioswebsocket
      if (this.host.userID) {
        this.$axios.get("http://localhost:8080/offLine", {
          params: { userID: this.host.userID },
        });
      }
      //websocket
      this.$store.dispatch("group/disconnect");
      //host
      //hostnativegroup
      this.destroyHost();
    };
    // or 
    this.toGroup();
  },
};
</script>


