<template>
  <div>
    <div class="head">
      <img :src="host.headImageAddress" />
      <h1>{{ host.userName }}</h1>
    </div>
    <div class="content">
      <div class="left-menu">
        <div @click="gClick" class="group-menu">
          <div
            class="menu-head"
            :style="{
              backgroundColor: groupClick ? '#FE8333' : '#fff',
            }"
          >
            <img
              :style="{ display: groupClick ? 'inline-block' : 'none' }"
              class="menu-head-img-open"
              src="image/.png"
            />
            <img
              :style="{ display: !groupClick ? 'inline-block' : 'none' }"
              class="menu-head-img-close"
              src="image/.png"
            />
            <p class="menu-title"></p>
            <img
              :style="{ display: groupClick ? 'inline-block' : 'none' }"
              class="menu-arrow-open"
              src="image/.png"
            />
            <img
              :style="{ display: !groupClick ? 'inline-block' : 'none' }"
              class="menu-arrow-close"
              src="image/.png"
            />
          </div>
          <ul
            :style="{ display: groupClick ? 'block' : 'none' }"
            class="menu-list"
          >
            <li>
              <!-- <a href="#"></a> -->
              <router-link to="/group/block"></router-link>
            </li>
          </ul>
        </div>
        <div @click="sClick" class="setting-menu">
          <div
            class="menu-head"
            :style="{
              backgroundColor: settingClick ? '#FE8333' : '#fff',
            }"
          >
            <img
              :style="{ display: settingClick ? 'inline-block' : 'none' }"
              class="menu-head-img-open"
              src="image/.png"
            />
            <img
              :style="{ display: !settingClick ? 'inline-block' : 'none' }"
              class="menu-head-img-close"
              src="image/.png"
            />
            <p class="menu-title"></p>
            <img
              :style="{ display: settingClick ? 'inline-block' : 'none' }"
              class="menu-arrow-open"
              src="image/.png"
            />
            <img
              :style="{ display: !settingClick ? 'inline-block' : 'none' }"
              class="menu-arrow-close"
              src="image/.png"
            />
          </div>
          <ul
            :style="{ display: settingClick ? 'block' : 'none' }"
            class="menu-list"
          >
            <li>
              <!-- setting -->
              <router-link to="/group/setting"></router-link>
            </li>
            <li>
              <!-- login -->
              <a @click="reLogin" href="#"></a>
            </li>
          </ul>
        </div>
      </div>
      <!-- <Block> <Edit> <Setting>-->
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import vue from "../../main";
export default {
  name: "Group",
  data() {
    return {
      groupClick: false,
      settingClick: false,
    };
  },
  computed: {
    ...mapState("group", ["host"]),
  },
  methods: {
    ...mapActions("group", ["disconnect", "destroyMsg", "destroyHost"]),
    gClick() {
      this.groupClick = !this.groupClick;
    },
    sClick() {
      this.settingClick = !this.settingClick;
    },
    reLogin() {
      // 
      localStorage.clear();
      //
      this.offLine();
    },
    offLine() {
      //
      this.$axios
        .get("http://localhost:8080/offLine", {
          params: { userID: this.host.userID },
        })
        .then((res) => {
          //login replace
          this.$router.replace({
            name: "login",
          });
        })
        .catch((res) => {
          alert("" + "\n" + res.message);
        });
    },
    beforeUpdate() {
      console.log("GroupBeforeUpdate");
    },
    updated() {
      console.log("GroupUpdated");
    },
  },

  // GroupNative,
  // 
  // hostbeforeRouteEnteruserID

  // replacegroup
  // ,
  // group,beforeRouteEnter,vue
  // ,mountedgroup,beforeRouteEnter
  // vue
  beforeRouteLeave(to, from, next) {
    if (to.name === "login") {
      //
      Object.getPrototypeOf(vue).$axios.get("http://localhost:8080/offLine", {
        params: { userID: vue.$store.state.group.host.userID },
      });
      //Setting,
      Object.getPrototypeOf(vue).$bus.$emit("destroySetting");
      vue.$store.state.group.host = {};
      vue.$store.dispatch("group/destroyMsg");
      localStorage.clear();
    }
    next();
  },
  beforeRouteEnter(to, from, next) {
    //!vuevuevue
    //App.vue
    //logingroup
    if (!vue) {
      return;
    }
    if (!vue.$store.state.group.host.userID) {
      alert("");
      //
      vue.$router.replace({
        name: "login",
      });
      return;
    }
    next();
  },
};
</script>

<style>
body {
  background-color: rgba(245, 245, 245);
}

hr {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.head {
  width: 100%;
  height: 280px;
  background-color: #7cc2fd;
  text-align: center;
  margin-bottom: 20px;
}

.head img {
  width: 125px;
  height: 125px;
  border-radius: 100%;
  margin-top: 50px;
  border: 5px solid rgba(255, 255, 255, 0.3);
}

.content {
  margin: auto;
  width: 1500px;
  overflow: hidden;
}

.left-menu {
  float: left;
  margin-left: 100px;
  width: 220px;
  text-align: center;
  background-color: #fff;
}

.menu-head {
  background-color: #fff;
  padding: 20px 0;
  color: #000;
  font-size: 16px;
  cursor: pointer;
}

.menu-head-click {
  background-color: rgb(254, 131, 51);
  padding: 20px 0;
  color: #fff;
  font-size: 16px;
}

.menu-head-img-open {
  vertical-align: middle;
}

.menu-head-img-close {
  vertical-align: middle;
}

.menu-title {
  display: inline-block;
  vertical-align: middle;
}

.menu-arrow-open {
  vertical-align: middle;
}

.menu-arrow-close {
  display: inline-block;
  vertical-align: middle;
}

.menu-list li {
  text-align: center;
  line-height: 60px;
}
.menu-list li a {
  color: #a5a5a5;
  font-size: 16px;
}
</style>